  padding: 0;
  margin: 0;
  list-style: none;
}

.custom-disk li {
  padding-left: 15px;
  position: relative;
}
.custom-disk li:before {
  content: "";
  width: 4px;
  position: absolute;
  height: 4px;
  left: 0;
  top: 8px;
  background: #202020;
  border-radius: 100%;
}

.flex-align {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.vertical-align {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.vertical-align * {
  margin: auto 0;
}

.img-cover {
  position: relative;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center top;
  background-repeat: no-repeat;
}
.img-cover img {
  visibility: hidden;
  -o-object-fit: cover;
     object-fit: cover;
  width: 100%;
  position: absolute;
  height: 100%;
  left: 0;
  top: 0;
}

