.btn {
  text-decoration: none;
  font-weight: 600;
  min-width: 180px;
  padding: 12px 15px;
  font-size: 15px;
  display: inline-block;
  border-radius: 30px;
  cursor: pointer;
  text-align: center;
  background: none;
  background-color: #fff;
  border-color: #fff;
  -webkit-transition: ease-out 0.5s;
  transition: ease-out 0.5s;
  -webkit-box-shadow: rgba(38, 57, 77, 0.4) 0px 20px 30px -10px;
          box-shadow: rgba(38, 57, 77, 0.4) 0px 20px 30px -10px;
  border: 1px solid;
}
.btn.active, .btn:hover {
  text-decoration: none;
  -webkit-box-shadow: rgba(38, 57, 77, 0.71) 0px 20px 30px -10px;
          box-shadow: rgba(38, 57, 77, 0.71) 0px 20px 30px -10px;
}

.ico-in i, .ico-in span {
  vertical-align: middle;
  display: inline-block;
}
.ico-in i {
  font-size: 0;
  position: relative;
}
.ico-in .back-ico {
  visibility: hidden;
}
.ico-in .front-ico {
  position: absolute;
  left: 0;
  top: 0;
}
.ico-in.active, .ico-in:hover {
  text-decoration: none;
}
.ico-in.active .back-ico, .ico-in:hover .back-ico {
  visibility: visible;
}
.ico-in.active .front-ico, .ico-in:hover .front-ico {
  visibility: hidden;
}

