/*pagination page start*/
.pagination {
  -ms-grid-column-span: 16;
  grid-column: span 16;
  text-align: center;
  margin: 60px 0 45px;
  position: relative;
  z-index: 2;
}
.pagination a, .pagination span {
  width: 30px;
  height: 30px;
  border-radius: 100%;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  line-height: 27px;
  border: 1px solid #202020;
  vertical-align: middle;
  margin: 0 15px;
}
.pagination a.current, .pagination a:hover, .pagination span.current, .pagination span:hover {
  color: #fff;
  background-color: #202020;
}

.next.page-numbers,
.prev.page-numbers,
.pagination-prev,
.pagination-next {
  background-size: 100%;
  font-size: 0;
  background-color: #202020;
}

.pagination-dotted {
  background-color: initial;
  color: inherit;
}

.prev.page-numbers,
a.pagination-prev {
  display: inline-block;
  vertical-align: middle;
  width: 30px;
  height: 30px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../images/controls/arrow-left-white.svg");
}
.prev.page-numbers:hover,
a.pagination-prev:hover {
  background-color: #fff;
  background-image: url("../images/controls/arrow-left-black.svg");
}

.next.page-numbers,
a.pagination-next {
  display: inline-block;
  vertical-align: middle;
  width: 30px;
  height: 30px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../images/controls/arrow-right-white.svg");
}
.next.page-numbers:hover,
a.pagination-next:hover {
  background-color: #fff;
  background-image: url("../images/controls/arrow-right-black.svg");
}

/*pagination page end*/
