}

.back-link {
  text-decoration: none;
  font-family: "bold";
  color: #202020;
  font-size: 15px;
}
.back-link i {
  display: inline-block;
  vertical-align: middle;
  width: 30px;
  height: 30px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../images/controls/arrow-left-black.svg");
  border-radius: 100%;
  border: 1px solid;
  margin-right: 5px;
}
.back-link:hover {
  color: #75A015;
}
.back-link:hover i {
  background-image: url("../images/controls/arrow-left-white.svg");
  background-color: #75A015;
  border-color: #75A015;
}

.switch-btn {
  border: 1px solid;
  width: 60px;
  height: 30px;
  border-radius: 15px;
  cursor: pointer;
  display: inline-block;
  margin: 0 9px;
}
.switch-btn span, .switch-btn i {
  vertical-align: middle;
}
.switch-btn i {
  position: relative;
  margin: 2px;
  display: inline-block;
  width: calc(50% - 5px);
  height: calc(100% - 4px);
  background-color: #fff;
  border-radius: 100%;
  left: 0;
  -webkit-transition: ease-out 0.25s;
  transition: ease-out 0.25s;
}

.switch-btn--orange {
  color: #fba72a;
  font-weight: bold;
}
.switch-btn--orange i {
  background-color: #fba72a;
}

.switch-btn--active i {
  left: 50%;
}

