/*-file-upload-*/
.file-input-container {
  position: relative;
}

.input-file {
  visibility: hidden;
  position: absolute;
  margin-top: -20px;
}

.file-decorate {
  position: relative;
  cursor: pointer;
}
.file-decorate span {
  padding-left: 25px;
  position: relative;
  display: inline-block;
}
.file-decorate span::after {
  content: "";
  width: 20px;
  position: absolute;
  height: 20px;
  left: 0;
  top: 0;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../images/controls/file.svg");
}
.file-decorate i {
  width: 30px;
  position: absolute;
  display: none;
  height: 30px;
  z-index: 2;
  right: 0;
  top: 0px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../images/controls/close-black.svg");
  margin: 10px;
  cursor: pointer;
}

.file-input-container.full i {
  display: block;
}

.notice-file {
  position: absolute;
  right: 0;
  top: 0;
  display: none;
}
.notice-file i {
  display: inline-block;
  vertical-align: middle;
  width: 30px;
  height: 30px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../images/icons/danger-sign-black.svg");
  margin-left: 15px;
}

.notice-file--show {
  display: block;
}

.hint-input-file {
  color: #54565B;
  padding: 5px;
  border: 1px solid;
  border-bottom: 0;
}

/*-file-upload-*/
/*-custom-select-*/
.custom-select {
  cursor: pointer;
  position: relative;
}
.custom-select ul {
  width: 100%;
  position: absolute;
  height: auto;
  z-index: 1;
  left: 0;
  top: 100%;
  opacity: 0;
  visibility: hidden;
  background: #fff;
  overflow: hidden;
  border-radius: 15px;
  -webkit-box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 9px 12px;
          box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 9px 12px;
  margin-left: -1px;
  width: calc(100% + 2px);
}
.custom-select ul li {
  padding: 10px 15px;
  cursor: pointer;
  -webkit-transition: ease-out 0.5s;
  transition: ease-out 0.5s;
}
.custom-select ul li:hover {
  background: #eaeaea;
}
.custom-select.active {
  z-index: 3;
}
.custom-select.active ul {
  visibility: visible;
  opacity: 1;
}
.custom-select.active i {
  -webkit-transform: translateY(-50%) rotate(180deg);
          transform: translateY(-50%) rotate(180deg);
}
.custom-select span {
  position: relative;
  z-index: 2;
}
.custom-select i {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../images/controls/carret-down-gray.svg");
  width: 30px;
  position: absolute;
  height: 30px;
  z-index: 2;
  right: 5px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

