/*-checkbox-start-*/
.checkbox,
.radio {
  display: inline-block;
  position: relative;
  padding: 5px;
  padding-left: 35px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin: 0 30px 15px 0;
}
.checkbox label,
.radio label {
  cursor: pointer;
}
.checkbox input,
.radio input {
  width: 100%;
  position: absolute;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
}
.checkbox input:checked ~ span,
.radio input:checked ~ span {
  -webkit-box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
          box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}
.checkbox input:checked ~ span:after,
.radio input:checked ~ span:after {
  display: block;
}
.checkbox span,
.radio span {
  width: 26px;
  position: absolute;
  height: 26px;
  left: 0px;
  top: 2px;
  border: 3px solid #fff;
  -webkit-box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 9px 12px;
          box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 9px 12px;
  border-radius: 4px;
  background-color: #fba72a;
  -webkit-transition: ease-out 0.5s;
  transition: ease-out 0.5s;
  cursor: pointer;
}
.checkbox span:after,
.radio span:after {
  content: "";
  width: 80%;
  position: absolute;
  height: 80%;
  left: 10%;
  top: 10%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../images/icons/ok-white.svg");
  display: none;
}

